import { Types } from '../types';

const initialState = {
  isLoading: false,
  hasError: false,
  deleteSuccess: false,
  errorMsg: '',
};

export function deleteUnitsReducer(state = initialState, action) {
  switch (action.type) {
    case Types.DELETE_UNITS_REQUEST:
      return {
        ...state,
        isLoading: true,
      };
    case Types.DELETE_UNITS_SUCCESS:
      return {
        ...state,
        isLoading: false,
        hasError: false,
        deleteSuccess: true,
      };
    case Types.DELETE_UNITS_FAILURE:
      return {
        ...state,
        isLoading: false,
        hasError: true,
        deleteSuccess: false,
        errorMsg: action.payload,
      };
    case Types.DELETE_UNITS_RESET_STATE:
      return initialState;
    default:
      return state;
  }
}
