import React from 'react';
import PropTypes from 'prop-types';

import Input from '../../../components/Input';
import icPerson from '../../../assets/images/ic_person.svg';
import Button from '../../../components/Button';

const FirstStep = ({ state, setState, alertMessage, eventButton }) => (
  <>
    <h4 className="commonFontStyle commonFontStyle--title">Redefinir senha</h4>
    <p className="commonFontStyle commonFontStyle--content">
      Digite abaixo o email cadastrado.
      <br />
      Enviaremos agora mesmo uma nova senha para recuperar seu acesso.
    </p>

    <Input
      icon={icPerson}
      alt="icone de pessoa, mostrando cabeça e ombros."
      title="icone de pessoa."
      placeholder="User Admin"
      setInputValue={setState}
      inputValue={state}
      typeInput="email"
    />

    {alertMessage && <div className="w-100 mt-2">{alertMessage}</div>}

    <div className="mt-5 mb-5">
      <Button inputFilled={state} handleClick={eventButton}>
        Receber nova senha
      </Button>
    </div>
  </>
);

FirstStep.propTypes = {
  state: PropTypes.string.isRequired,
  setState: PropTypes.func.isRequired,
  eventButton: PropTypes.func.isRequired,
  alertMessage: PropTypes.string,
};

FirstStep.defaultProps = {
  alertMessage: '',
};

export default FirstStep;
