import { Types } from '../types';

export function retrieveTeachersRowsTotalOffsetRequest(offset = 0, authToken, search) {
  return {
    type: Types.RETRIEVE_ALL_TEACHERS_REQUEST,
    payload: { offset, authToken, search },
  };
}

export function retrieveTeachersRowsTotalOffsetSuccess({ rows, total, offset }) {
  return {
    type: Types.RETRIEVE_ALL_TEACHERS_SUCCESS,
    payload: { rows, total, offset },
  };
}

export function retrieveTeachersRowsTotalOffsetFailure(errorMsg) {
  return {
    type: Types.RETRIEVE_ALL_TEACHERS_FAILURE,
    payload: errorMsg,
  };
}
