import React, { useEffect, useState, useCallback } from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import PropTypes from 'prop-types';

import DatePicker from 'react-datepicker';
import moment from 'moment';
import { retrieveAllDashboardRequest } from '../../../Redux/Dashboard/Retrieve/actions';

import Button from '../../../components/Button';

import { enableButton, makeQueryString } from './filterDashboardUtils';

import 'react-datepicker/dist/react-datepicker.css';

function FilterDashboard({ authToken }) {
  const { push, location } = useHistory();

  const dispacth = useDispatch();

  const [filterValues, setFilterValues] = useState({
    to: null,
    from: null,
  });

  const cbHandleChangeFilterFields = useCallback(HandleChangeFilterFields, [filterValues]);
  function HandleChangeFilterFields(name) {
    return value => {
      setFilterValues({
        ...filterValues,
        [name]: value,
      });
    };
  }

  const cbEnableButton = useCallback(enableButton, [filterValues])(filterValues);

  const formatDateToQuery = dateToFormat => moment(dateToFormat, 'yyyy-mm-dd', true).format();

  function handleOnSubmit(e) {
    e.preventDefault();
    const { pathname } = location;

    const formattedFilterValues = {
      from: formatDateToQuery(filterValues.from),
      to: formatDateToQuery(filterValues.to),
    };

    retrieveAllDashboard(formattedFilterValues);

    const search = makeQueryString(formattedFilterValues);

    push({
      pathname,
      search,
    });
  }

  function retrieveAllDashboard(formattedFilterValues) {
    dispacth(retrieveAllDashboardRequest({ ...formattedFilterValues, authToken }));
  }

  useEffect(retrieveAllDashboard, []);

  return (
    <form onSubmit={handleOnSubmit} className="filterDashBoard mb-4">
      <div className="InputLabel mr-3">
        <label htmlFor="from">De</label>
        <br />
        <DatePicker
          autoComplete="off"
          selected={filterValues.from}
          id="from"
          dateFormat="dd/MM/yyyy"
          onChange={cbHandleChangeFilterFields('from')}
        />
      </div>

      <div className="InputLabel">
        <label htmlFor="to">Até</label>
        <br />
        <DatePicker
          autoComplete="off"
          selected={filterValues.to}
          id="to"
          dateFormat="dd/MM/yyyy"
          onChange={cbHandleChangeFilterFields('to')}
        />
      </div>

      <div />

      <div className="col-2 filterDashBoard__button">
        <Button type="submit" inputFilled={cbEnableButton}>
          Atualizar Data
        </Button>
      </div>
    </form>
  );
}

FilterDashboard.propTypes = {
  authToken: PropTypes.string.isRequired,
};

export default FilterDashboard;
