import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import './ModalEditSelfUser.scss';

import Modal from '../../Modal';
import Thumbnail from '../../Thumbnail';
import { AuthRetrieveUserSelector, AuthTokenSelector } from '../../../Redux/Auth/selector';
import { logout } from '../../../Redux/Auth/actions';

import { ViewDataUser } from './ViewDataUser';
import { EditPassword } from './EditPassword';

import { updateSelfRequest } from '../../../Redux/Admin/UpdatedSelf/actions';

import { formateDate } from '../../../utils/formatDate';

import { ModalDispatch } from '../../../contexts/ModalContext';
import { LoadingDispatch } from '../../../contexts/LodingContext';

function ModalEditSelfUser() {
  const [editSelf, setEditSelf] = useState(false);
  const dispatch = useDispatch();
  const modalDispatch = ModalDispatch();
  const loadingDispatch = LoadingDispatch();
  const { name, username, id, created_at: createdAt } = useSelector(AuthRetrieveUserSelector);
  const authToken = useSelector(AuthTokenSelector);

  const dataOfUserToList = [
    { label: 'Adicionado em', value: formateDate(createdAt) },
    { label: 'Email', value: username },
    { label: 'Senha', value: '• • • • • • • • • •' },
  ];

  const changeToEdit = () => setEditSelf(!editSelf);

  const logoutOnClick = () => dispatch(logout());

  function updatePassword(password) {
    const params = {
      id,
      data: { password },
      authToken,
    };
    dispatch(updateSelfRequest(params));
    modalDispatch({ type: '' });
    loadingDispatch({ type: 'open' });
  }

  return (
    <Modal size="sm" alignHorizontal="right">
      <div className="bodyEditSelfUser">
        <div className="d-flex justify-content-center">
          <Thumbnail height="80px" alt="Foto de perfil" />
        </div>

        <div className="text-center mt-2">
          <div className="bodyEditSelfUser__nameText">{name}</div>
          <div className="bodyEditSelfUser__greyText">{username}</div>
        </div>

        {editSelf ? (
          <EditPassword email={username} updatePassword={updatePassword} returnToData={changeToEdit} />
        ) : (
          <ViewDataUser dataOfUserToList={dataOfUserToList} logoutOnClick={logoutOnClick} changeToEdit={changeToEdit} />
        )}
      </div>
    </Modal>
  );
}

export default ModalEditSelfUser;
