import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';

import Input from '../../../components/Input';
import Button from '../../../components/Button';

import { MacrothemessRetrieveAllSelectors } from '../../../Redux/Macrothemes/selectors';
import { AuthTokenSelector } from '../../../Redux/Auth/selector';

import { createMacrothemesRequest } from '../../../Redux/Macrothemes/Create/actions';
import { updateMacrothemesRequest } from '../../../Redux/Macrothemes/Update/actions';
import { deleteMacrothemesRequest } from '../../../Redux/Macrothemes/Delete/actions';

import { ModalDispatch } from '../../../contexts/ModalContext';
import { ModalToDeleteMacrothemes } from '../ModalToDeleteMacrothemes';

function MacrothemessInfo({ idMacrothemes, useForEdit, closeModalCreate }) {
  const dispatch = useDispatch();
  const [formState, setFormState] = useState({
    name: '',
    from_age: 0,
    to_age: 0
  })

  const handleChangeForm = nameField =>
    value => {
      const removeStringIfIsNumber = nameField === 'name' ? value : value.replace(/\D/, '')
      const updatedFormState = { ...formState, [nameField]: removeStringIfIsNumber }
      setFormState(updatedFormState)
    }

  const textButton = useForEdit ? 'Atualizar' : 'Cadastrar';

  const authToken = useSelector(AuthTokenSelector);
  const modalDispatch = ModalDispatch();

  function createMacrothemes(e) {
    e.preventDefault();
    const parameters = {
      data: formState,
      authToken,
    };
    dispatch(createMacrothemesRequest(parameters));
    closeModalCreate();
  }

  function updateMacrothemes(e) {
    e.preventDefault();
    const parameters = {
      id: idMacrothemes,
      data: formState,
      authToken,
    };
    dispatch(updateMacrothemesRequest(parameters));
  }

  function modalSelect(e) {
    e.preventDefault();
    const payload = <ModalToDeleteMacrothemes nameMacrothemes={formState.name} actionIfYes={deleteMacrothemes} />;
    modalDispatch({ type: 'open', payload });
  }
  function deleteMacrothemes() {
    dispatch(deleteMacrothemesRequest({ id: idMacrothemes, authToken }));
    modalDispatch({ type: '' });
  }

  const finalDispatch = useForEdit ? updateMacrothemes : createMacrothemes;

  function startComponent() {
    if (useForEdit) {
      const { name, from_age, to_age } = macrothemess.rows.find(val => val.id === idMacrothemes);
      setFormState({ name, from_age, to_age })
      // setName(Name);
    }
  }

  useEffect(() => {
    startComponent();
  }, []);
  const macrothemess = useSelector(MacrothemessRetrieveAllSelectors);

  return (
    <>
      <div className="mt-4 row">
        <form onSubmit={finalDispatch} className="col-12">
          <div className="row">
            <div className="col-4 mr-4">
              <label htmlFor="name">Nome do Macrotema</label>
              <Input
                id="name"
                inputValue={formState.name}
                setInputValue={handleChangeForm('name')}
                required
                maxLength={40}
              />
            </div>

            <div className="col-4 mr-4">
              <label htmlFor="name">Faixa etária inicial</label>
              <Input
                id="from_age"
                inputValue={formState.from_age}
                setInputValue={handleChangeForm('from_age')}
                // required
                maxLength={40}
              />
            </div>

            <div className="col-4 mr-4 mt-4">
              <label htmlFor="name">Faixa etária final</label>
              <Input
                id="to_age"
                inputValue={formState.to_age}
                setInputValue={handleChangeForm('to_age')}
                // required
                maxLength={40}
              />
            </div>
          </div>

          <div className="d-flex justify-content-end mt-1 mb-1">
            {useForEdit && (
              <div className="col-2">
                <Button inputFilled handleClick={modalSelect} type="button" grey>
                  Excluir
                </Button>
              </div>
            )}
            <div className="col-2">
              <Button inputFilled type="submit">
                {textButton}
              </Button>
            </div>
          </div>
        </form>
      </div>
    </>
  );
}

MacrothemessInfo.propTypes = {
  idMacrothemes: PropTypes.number,
  useForEdit: PropTypes.bool,
  closeModalCreate: PropTypes.func,
};

MacrothemessInfo.defaultProps = {
  idMacrothemes: 0,
  useForEdit: false,
  closeModalCreate: () => { },
};

export default MacrothemessInfo;
