import React, { useState } from 'react';
import PropTypes from 'prop-types';

import Button from '../../../Button';

function EditPassword({ email, updatePassword, returnToData }) {
  const [password, setPassword] = useState();
  const [checkPassword, setCheckPassword] = useState();
  const [inputFocus, setInputFocus] = useState({
    password: false,
    checkPassword: false,
  });

  const validatePassword = !!(password && checkPassword && password === checkPassword);

  function activeFocus(e) {
    const { name } = e.target;
    setInputFocus({
      ...inputFocus,
      [name]: !inputFocus[name],
    });
  }

  function handleSubmit(e) {
    e.preventDefault();
    updatePassword(password);
  }

  return (
    <form onSubmit={handleSubmit}>
      <div className="d-flex justify-content-between bodyEditSelfUser__dividerLines bodyEditSelfUser__fontSize">
        <div className="d-flex justify-content-start bodyEditSelfUser__greyText">Email</div>
        <div className="d-flex justify-content-end">{email}</div>
      </div>

      <div
        className={`d-flex justify-content-between bodyEditSelfUser__dividerLines ${inputFocus.password &&
          'bodyEditSelfUser__dividerLines--inFocus'} bodyEditSelfUser__fontSize`}
      >
        <div className="d-flex justify-content-start">
          <label htmlFor="password" className=" bodyEditSelfUser__greyText">
            Nova Senha
          </label>
        </div>
        <div className="d-flex justify-content-end">
          <input
            type="password"
            id="password"
            name="password"
            onChange={e => setPassword(e.target.value)}
            className="text-right inputOcult"
            onFocus={activeFocus}
            onBlur={activeFocus}
            autoFocus
          />
        </div>
      </div>

      <div
        className={`d-flex justify-content-between bodyEditSelfUser__dividerLines ${inputFocus.checkPassword &&
          'bodyEditSelfUser__dividerLines--inFocus'} bodyEditSelfUser__fontSize`}
      >
        <div className="d-flex justify-content-start">
          <label htmlFor="checkPassword" className=" bodyEditSelfUser__greyText">
            {' '}
            Confirmar Senha
          </label>
        </div>
        <div className="d-flex justify-content-end">
          <input
            type="password"
            id="checkPassword"
            name="checkPassword"
            onChange={e => setCheckPassword(e.target.value)}
            onFocus={activeFocus}
            onBlur={activeFocus}
            className="text-right inputOcult"
          />
        </div>
      </div>

      <div className="col mt-4 mb-3">
        <div className="row">
          <div className="col">
            <Button handleClick={returnToData} grey inputFilled>
              Cancelar
            </Button>
          </div>
          <div className="col">
            <Button type="submit" inputFilled={validatePassword}>
              Atualizar
            </Button>
          </div>
        </div>
      </div>
    </form>
  );
}

EditPassword.propTypes = {
  email: PropTypes.string.isRequired,
  updatePassword: PropTypes.func.isRequired,
  returnToData: PropTypes.func.isRequired,
};

export default EditPassword;
