import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';

import MultiSelect from 'react-select';
import Input from '../../../components/Input';
import Button from '../../../components/Button';

import { UnitssRetrieveAllSelectors } from '../../../Redux/Units/selectors';
import { WorkshopsRetrieveAllSelectors } from '../../../Redux/Workshop/selectors';
import { AuthTokenSelector } from '../../../Redux/Auth/selector';

import { createWorkshopRequest } from '../../../Redux/Workshop/Create/actions';
import { updateWorkshopRequest } from '../../../Redux/Workshop/Update/actions';
import { deleteWorkshopRequest } from '../../../Redux/Workshop/Delete/actions';

import { ModalDispatch } from '../../../contexts/ModalContext';
import { ModalToDeleteWorkshop } from '../ModalToDeleteWorkshop';

function WorkshopsInfo({ idWorkshop, useForEdit, closeModalCreate }) {
  const dispatch = useDispatch();
  const [name, setName] = useState();
  const [site, setSite] = useState();

  const sitesSelector = useSelector(UnitssRetrieveAllSelectors).rows.map(val => {
    const { id: value, name: label } = val;
    return { value, label };
  });

  const textButton = useForEdit ? 'Atualizar' : 'Cadastrar';

  const authToken = useSelector(AuthTokenSelector);
  const modalDispatch = ModalDispatch();

  function createWorkshop(e) {
    e.preventDefault();
    const parameters = {
      data: { name, site_id: site.value },
      authToken,
    };
    dispatch(createWorkshopRequest(parameters));
    closeModalCreate();
  }

  function updateWorkshop(e) {
    e.preventDefault();
    const parameters = {
      id: idWorkshop,
      data: { name, site_id: site.value },
      authToken,
    };
    dispatch(updateWorkshopRequest(parameters));
  }

  function modalSelect(e) {
    e.preventDefault();
    const payload = <ModalToDeleteWorkshop nameWorkshop={name} actionIfYes={deleteWorkshop} />;
    modalDispatch({ type: 'open', payload });
  }
  function deleteWorkshop() {
    dispatch(deleteWorkshopRequest({ id: idWorkshop, authToken }));
    modalDispatch({ type: '' });
  }

  const finalDispatch = useForEdit ? updateWorkshop : createWorkshop;

  function startComponent() {
    if (useForEdit) {
      const { name: Name, site_id } = workshops.rows.find(val => val.id === idWorkshop);
      setName(Name);
      setSite(site_id);
    }
  }

  useEffect(() => {
    startComponent();
  }, []);
  const workshops = useSelector(WorkshopsRetrieveAllSelectors);

  return (
    <>
      <div className="mt-4 row">
        <form onSubmit={finalDispatch} className="col-12">
          <div className="row">
            <div className="col-4 mr-4">
              <label htmlFor="name">Nome</label>
              <Input id="name" inputValue={name} setInputValue={setName} required maxLength={40} />
            </div>
            <div className="col-4 mr-4">
              <label htmlFor="name">Unidade</label>
              <MultiSelect value={site} onChange={setSite} options={sitesSelector} />
            </div>
          </div>

          <div className="d-flex justify-content-end mt-1 mb-1">
            {useForEdit && (
              <div className="col-2">
                <Button inputFilled handleClick={modalSelect} type="button" grey>
                  Excluir
                </Button>
              </div>
            )}
            <div className="col-2">
              <Button inputFilled type="submit">
                {textButton}
              </Button>
            </div>
          </div>
        </form>
      </div>
    </>
  );
}

WorkshopsInfo.propTypes = {
  idWorkshop: PropTypes.number,
  useForEdit: PropTypes.bool,
  closeModalCreate: PropTypes.func,
};

WorkshopsInfo.defaultProps = {
  idWorkshop: 0,
  useForEdit: false,
  closeModalCreate: () => {},
};

export default WorkshopsInfo;
