import React from 'react';
import PropTypes from 'prop-types';
import Modal from '../../../components/Modal';
import Button from '../../../components/Button';

import { ModalDispatch } from '../../../contexts/ModalContext';

function ModalToDeleteAdmin({ nameAdmin, actionIfYes }) {
  const modalDispatch = ModalDispatch();
  return (
    <Modal size="sm" alignHorizontal="center" centralizeVertical>
      <div className="contentModalToDelete">{`Você tem certeza que deseja excluir o/a admin ${nameAdmin}?`}</div>

      <div className="mt-5 mb-4 d-flex justify-content-center">
        <div className="col-md-4">
          <Button inputFilled type="button" handleClick={() => modalDispatch({ type: '' })} grey>
            Não
          </Button>
        </div>
        <div className="col-md-4">
          <Button inputFilled type="button" handleClick={actionIfYes}>
            Sim
          </Button>
        </div>
      </div>
    </Modal>
  );
}

ModalToDeleteAdmin.propTypes = {
  nameAdmin: PropTypes.string.isRequired,
  actionIfYes: PropTypes.string.isRequired,
};

export default ModalToDeleteAdmin;
