import { Types } from '../types';

const initialState = {
  data: [],
  isLoading: false,
  hasError: false,
  errorMsg: '',
};

export function retriveCitiesOfStateReducer(state = initialState, action) {
  const { data } = action.payload || state;
  switch (action.type) {
    case Types.RETRIEVE_CITIES_OF_STATE_REQUEST:
      return {
        ...state,
        isLoading: true,
      };
    case Types.RETRIEVE_CITIES_OF_STATE_SUCCESS:
      return {
        ...state,
        isLoading: false,
        data,
      };
    case Types.RETRIEVE_CITIES_OF_STATE_FAILURE:
      return {
        ...state,
        isLoading: false,
        hasError: true,
        errorMsg: action.payload,
      };
    default:
      return state;
  }
}
