import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import icSearch from '../../../assets/images/ic_search.svg';

import { AuthTokenSelector } from '../../../Redux/Auth/selector';
import { retrieveAdminsRowsTotalOffsetRequest } from '../../../Redux/Admin/Retrieve/actions';
import { retrieveMentorsRowsTotalOffsetRequest } from '../../../Redux/Mentor/Retrieve/actions';
import { retrieveMacrothemessRowsTotalOffsetRequest } from '../../../Redux/Macrothemes/Retrieve/actions';
import { retrieveUnitssRowsTotalOffsetRequest } from '../../../Redux/Units/Retrieve/actions';
import { retrieveWorkshopsRowsTotalOffsetRequest } from '../../../Redux/Workshop/Retrieve/actions';
import { retrieveTeachersRowsTotalOffsetRequest } from '../../../Redux/Teacher/Retrieve/actions';
import { retrieveClassesRowsTotalOffsetRequest } from '../../../Redux/Classes/Retrieve/actions';
import { retrieveStudentsRowsTotalOffsetRequest } from '../../../Redux/Students/Retrieve/actions';

const searchRoutes = {
  '/admins': retrieveAdminsRowsTotalOffsetRequest,
  '/mentores': retrieveMentorsRowsTotalOffsetRequest,
  '/professores': retrieveTeachersRowsTotalOffsetRequest,
  '/aulas': retrieveClassesRowsTotalOffsetRequest,
  '/alunos': retrieveStudentsRowsTotalOffsetRequest,
  '/oficinas': retrieveWorkshopsRowsTotalOffsetRequest,
  '/unidades': retrieveUnitssRowsTotalOffsetRequest,
  '/artigos': retrieveMacrothemessRowsTotalOffsetRequest,
};

function InputTopBar() {
  const history = useHistory();
  const dispatch = useDispatch();
  const [find, setFind] = useState();

  const { pathname } = history.location;
  const authToken = useSelector(AuthTokenSelector);

  function handleSubmit(e) {
    e.preventDefault();
    const search = find ? `query=${find}` : '';
    history.push({ pathname, search });
    return searchRoutes[pathname] ? dispatch(searchRoutes[pathname](0, authToken, search)) : null;
  }

  const handleChange = ({ target: { value } }) => setFind(value);

  useEffect(() => {
    setFind('');
  }, [pathname]);

  return (
    <form onSubmit={handleSubmit}>
      <input
        type="text"
        name="query"
        placeholder="Pesquisar por nome, matéria…"
        value={find}
        onChange={handleChange}
        className="topBar__inputSearch"
        title="Pressionar enter para fazer a pesquisa"
        autoComplete="off"
      />
      <button type="submit" className="topBar__buttonSearch">
        <img src={icSearch} alt="lupa" />
      </button>
    </form>
  );
}

export default InputTopBar;
