import React from 'react';
import './styles.scss';
import { useHistory } from 'react-router-dom';
import { useSelector } from 'react-redux';

import { AuthTokenSelector } from '../../Redux/Auth/selector';

import {
  DashboardRetrieveAllDashboardSelector,
  DashboardRetrieveAllIsLoadingSelector,
} from '../../Redux/Dashboard/selectors';

import { CardItem } from '../../components/CardItem';

import { FilterDashboard } from './filterDashboard';

import { LoadingContent } from '../Classes/ModalContentsClass/LoadingContent';

function Dashboard() {
  const {
    location: { search },
  } = useHistory();

  const authToken = useSelector(AuthTokenSelector);
  const dashboard = useSelector(DashboardRetrieveAllDashboardSelector);
  const loading = useSelector(DashboardRetrieveAllIsLoadingSelector);
  return (
    <>
      <FilterDashboard authToken={authToken} />
      {loading ? (
        <LoadingContent />
      ) : (
        <div className="containerCard mt-4">
          {dashboard.map(item => {
            const { backendTitle, value, title, order } = item;
            const queryStrings = search ? `${search}&token=${authToken}` : `?token=${authToken}`;
            const url = `${window.location.protocol}//${window.location.host}/dashboard/${backendTitle}/xls${queryStrings}`;
            // const url = `${'https://appestudogf-homolog.azurewebsites.net'}/dashboard/${backendTitle}/xls?token=${authToken}`;
            return (
              <a className="changeAnchor" target="_blank" href={url} key={order}>
                <CardItem title={title} value={value} />
              </a>
            );
          })}
        </div>
      )}
    </>
  );
}

export default Dashboard;
