import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import './FormLogin.scss';

import icWarning from '../../../assets/images/ic_warning.svg';
import icPerson from '../../../assets/images/ic_person.svg';
import icLocked from '../../../assets/images/ic_locked.svg';

import Input from '../../../components/Input';
import Button from '../../../components/Button';
import Alert from '../../../components/Alert';

function FormLogin({ onClick, errorMsg, hasError }) {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const [inputFilled, setInputFilled] = useState('button--Await');

  const validateInputFields = () => {
    if (username && password) setInputFilled(true);
    else setInputFilled(false);
  };

  function showAlertError() {
    return (
      hasError && (
        <div className="col-12 mt-2">
          <Alert type="danger" messageAlert={errorMsg} icon={icWarning} altIcon="simbolo de atenção" />
        </div>
      )
    );
  }

  function handleSubmit(e) {
    e.preventDefault();
    onClick(username, password);
  }

  useEffect(() => validateInputFields());

  return (
    <form className="w-100" onSubmit={handleSubmit}>
      <div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12">
        <Input
          icon={icPerson}
          alt="icone de pessoa, mostrando cabeça e ombros."
          title="icone de pessoa."
          placeholder="User Admin"
          setInputValue={setUsername}
          inputValue={username}
          typeInput="text"
          dataTest="inputEmail"
        />

        <div className="mt-1">
          <Input
            icon={icLocked}
            alt="icone de um cadeado."
            title="icone de cadeado."
            placeholder="Senha"
            setInputValue={setPassword}
            inputValue={password}
            typeInput="password"
            dataTest="inputSenha"
          />
        </div>
      </div>

      {showAlertError()}

      <div className="col-12 mt-3 text-center" hidden>
        <Link to="/forgetpassword" className="forgotPasswordText" dataTest="forgotPassword">
          Esqueci minha senha
        </Link>
      </div>

      <div className="col-12 mt-4 mb-5">
        <Button dataTest="buttonLoggin" inputFilled={inputFilled} type="submit">
          Entrar
        </Button>
      </div>
    </form>
  );
}

FormLogin.propTypes = {
  onClick: PropTypes.func.isRequired,
  errorMsg: PropTypes.string,
  hasError: PropTypes.bool,
};

FormLogin.defaultProps = {
  errorMsg: '',
  hasError: false,
};

export default FormLogin;
