import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';

import MultiSelect from 'react-select';

import { Select } from '../../../components/Select';
import Thumbnail from '../../../components/Thumbnail';
import Input from '../../../components/Input';
import Button from '../../../components/Button';

import listOfState from '../../../utils/listOfState';
import { maskToTelephone, maskToCpf } from '../../../utils/maskToInputs';

import { TeachersRetrieveAllSelectors } from '../../../Redux/Teacher/selectors';
import { AuthTokenSelector } from '../../../Redux/Auth/selector';

import { createTeacherRequest } from '../../../Redux/Teacher/Create/actions';
import { updateTeacherRequest } from '../../../Redux/Teacher/Update/actions';
import { deleteTeacherRequest } from '../../../Redux/Teacher/Delete/actions';

import { retriveCitiesOfStateReducerRequest } from '../../../Redux/Cities/Retrieve/actions';
import { RetrieveCitiesSelectors } from '../../../Redux/Cities/selectors';

import { retrieveAllSubjectsRequest } from '../../../Redux/Subjects/Retrieve/actions';
import { SubjectsRetrieveAllSelectors } from '../../../Redux/Subjects/selectors';

import { retrieveSchoolsRequest } from '../../../Redux/Schools/Retrieve/actions';
import { retrieveSchoolsSelectors } from '../../../Redux/Schools/selectors';

import { ModalDispatch } from '../../../contexts/ModalContext';
import { ModalToDeleteTeacher } from '../ModalToDeleteTeacher';
import { BUTTON_CLASSES } from '../../../utils/enums';

function TeachersInfo({ idTeacher, useForEdit, closeModalCreate }) {
  const dispatch = useDispatch();
  const modalDispatch = ModalDispatch();

  const currentYear = new Date().getFullYear();

  const [name, setName] = useState();
  const [email, setEmail] = useState();
  const [graduationYear, setGraduationYear] = useState(currentYear);
  const [graduationUniversityName, setGraduationUniversityName] = useState();
  const [workSchoolName, setWorkSchoolName] = useState();
  const [countryState, setCountryState] = useState('AC');
  const [city, setCity] = useState('Acrelândia');
  const [cpf, setCpf] = useState();
  const [telephone, setTelephone] = useState();
  const [enabled, setEnabled] = useState(false);

  // change this
  const [subject, setSubject] = useState([]);
  const textButton = useForEdit ? 'Atualizar' : 'Cadastrar';

  const subjectsAll = useSelector(SubjectsRetrieveAllSelectors).map(val => {
    const { id: value, name: label } = val;
    return { value, label };
  });

  function getParamenters() {
    let Cpf = '';
    let Telephone = '';
    let DDD = '';
    if (cpf) {
      Cpf = cpf.replace(/\./g, '').replace(/-/g, '');
    }

    if (telephone.length === 14 || telephone.length === 15) {
      const telBase = telephone
        .replace(/-/g, '')
        .replace(/\(/g, '')
        .replace(/\)/g, '')
        .replace(' ', '');
      DDD = telBase.substring(0, 2);
      Telephone = telBase.substring(2, 11);
    } else if (telephone.length === 11 || telephone.length === 10) {
      DDD = telephone.substring(0, 2);
      Telephone = telephone.substring(2, 11);
    }
    // TODO
    const subjects = subject.map(val => val.value);
    // console.log(subjects)
    return {
      data: {
        name,
        email,
        graduation_year: String(graduationYear),
        graduation_university_name: graduationUniversityName,
        work_school_name: workSchoolName,
        state: countryState,
        city,
        cpf: Cpf,
        ddd: DDD,
        telephone: Telephone,
        enabled,
        subjects,
      },
      authToken,
    };
  }

  function createTeacher(e) {
    e.preventDefault();
    dispatch(createTeacherRequest(getParamenters()));
    closeModalCreate();
  }

  function updateTeacher(e) {
    e.preventDefault();
    const paramenters = getParamenters();
    paramenters.id = idTeacher;
    delete paramenters.data.email;
    delete paramenters.data.cpf;
    delete paramenters.data.subjectId;
    dispatch(updateTeacherRequest(paramenters));
  }

  function modalSelect(e) {
    e.preventDefault();
    const payload = <ModalToDeleteTeacher nameTeacher={name} actionIfYes={deleteTeacher} />;
    modalDispatch({ type: 'open', payload });
  }

  function deleteTeacher() {
    dispatch(deleteTeacherRequest({ id: idTeacher, authToken }));
    modalDispatch({ type: '' });
  }

  function startComponent() {
    if (useForEdit) {
      const {
        name: Name,
        username,
        graduation_year: GraduationYear,
        graduation_university_name: GraduationUniversityName,
        work_school_name: WorkSchoolName,
        cpf: Cpf,
        state: StateCoutry,
        city: City,
        ddd: DDD,
        telephone: Telephone,
        subjects: Subjects,
      } = teachers.rows.find(val => val.user_id === idTeacher);

      setName(Name);
      setEmail(username);
      setGraduationYear(GraduationYear);
      setGraduationUniversityName(GraduationUniversityName);
      setWorkSchoolName(WorkSchoolName);
      setCpf(Cpf);
      setCountryState(StateCoutry);
      setCity(City);
      const finalSubjects = Subjects.map(val => {
        const { id: value, name: label } = val;
        return { value, label };
      });
      setSubject(finalSubjects);
      setTelephone(DDD + Telephone);
    }
  }

  function getCities() {
    dispatch(retriveCitiesOfStateReducerRequest(countryState, authToken));
  }

  const finalDispatch = useForEdit ? updateTeacher : createTeacher;

  useEffect(() => {
    dispatch(retrieveAllSubjectsRequest(authToken));
    startComponent();
  }, []);
  useEffect(() => {
    getCities();
  }, [countryState]);

  useEffect(() => {
    dispatch(retrieveSchoolsRequest(countryState, city, authToken));
  }, [countryState, city]);

  const teachers = useSelector(TeachersRetrieveAllSelectors);
  const authToken = useSelector(AuthTokenSelector);
  const cities = useSelector(RetrieveCitiesSelectors).sort();
  const schools = useSelector(retrieveSchoolsSelectors);

  function getYears() {
    const year = new Date().getFullYear();
    const dates = [];
    for (let i = 1900; i <= year; i += 1) {
      dates.push(i);
    }
    return dates.reverse();
  }

  return (
    <>
      {useForEdit && <h2 style={{ color: 'black', padding: '0 0 20px 4px', fontWeight: 'bold' }}>{name}</h2>}

      <div>
        <Thumbnail alt="foto do professor" height="104px" />
      </div>

      <div className="mt-4 row">
        <form onSubmit={finalDispatch} className="col-12">
          <div className="row">
            <div className="col-3 mr-4">
              <label htmlFor="name">Nome</label>
              <Input id="name" inputValue={name} setInputValue={setName} required maxLength={40} />
            </div>

            <div className="col-3 mr-4">
              <label htmlFor="telephone">DDD + Telefone</label>
              <Input
                id="telephone"
                inputValue={maskToTelephone(telephone)}
                typeInput=""
                setInputValue={setTelephone}
                placeholder="( ) _ _ _ _ _ _ _ _ _"
                maxLength={15}
              />
            </div>

            <div className="col-3 mr-4">
              <label htmlFor="graduationYear">Ano da formatura</label>
              <br />
              <Select valueOfSelect={graduationYear} setValueOfSelect={setGraduationYear}>
                {getYears().map(val => (
                  <option key={val.toString()} value={val}>
                    {val}
                  </option>
                ))}
              </Select>
            </div>
          </div>

          <div className="row mt-4">
            <div className="col-3 mr-4">
              <label htmlFor="cpf">CPF</label>
              <Input
                id="cpf"
                readOnly={useForEdit}
                placeholder="_ _ _ . _ _ _ . _ _ _ - _ _ "
                inputValue={maskToCpf(cpf)}
                typeInput="text"
                setInputValue={setCpf}
                required
                maxLength={14}
              />
            </div>

            <div className="col-3 mr-4">
              <div className="row">
                <div className="col-6">
                  <label htmlFor="state">Estado</label>
                  <br />
                  <Select valueOfSelect={countryState} setValueOfSelect={setCountryState}>
                    {listOfState.map(val => (
                      <option key={val.initials} value={val.initials}>
                        {val.name}
                      </option>
                    ))}
                  </Select>
                </div>

                <div className="col-6">
                  <label htmlFor="city">Cidade</label>
                  <br />
                  <Select valueOfSelect={city} setValueOfSelect={setCity} deactivate={!cities.length > 0}>
                    {cities.map(val => (
                      <option key={val} value={val}>
                        {val}
                      </option>
                    ))}
                  </Select>
                </div>
              </div>
            </div>

            <div className="col-3">
              <label htmlFor="workSchoolName">Escola em que trabalha</label>
              <Select
                valueOfSelect={workSchoolName}
                setValueOfSelect={setWorkSchoolName}
                deactivate={!schools.data.length > 0}
              >
                {schools.data.map(item => {
                  const { nome_escola: schoolName } = item;
                  return (
                    <option key={schoolName.toString()} value={schoolName}>
                      {schoolName}
                    </option>
                  );
                })}
              </Select>
            </div>
          </div>

          <div className="row mt-4">
            <div className="col-3 mr-4">
              <label htmlFor="email">Email</label>
              <Input
                id="email"
                readOnly={useForEdit}
                inputValue={email}
                typeInput="email"
                setInputValue={setEmail}
                required
              />
            </div>

            <div className="col-3 mr-4">
              <label htmlFor="graduationUniversityName">Universidade de formação</label>
              <Input
                id="graduationUniversityName"
                inputValue={graduationUniversityName}
                typeInput="text"
                setInputValue={setGraduationUniversityName}
                required
              />
            </div>

            <div className="col-3">
              <label htmlFor="classes">Disciplina que leciona</label>
              <MultiSelect value={subject} onChange={setSubject} isMulti options={subjectsAll} />
            </div>
          </div>

          <div className="d-flex justify-content-end mt-2 mb-1">
            {useForEdit && (
              <div className="col-2">
                <Button handleClick={modalSelect} type="button" classes={BUTTON_CLASSES.GREY}>
                  Excluir
                </Button>
              </div>
            )}
            <div className="col-2">
              <Button type="submit" classes={BUTTON_CLASSES.NO_AWAIT}>
                {textButton}
              </Button>
            </div>
          </div>
        </form>
      </div>
    </>
  );
}

TeachersInfo.propTypes = {
  idTeacher: PropTypes.number,
  useForEdit: PropTypes.bool,
  closeModalCreate: PropTypes.func,
};

TeachersInfo.defaultProps = {
  idTeacher: 0,
  useForEdit: false,
  closeModalCreate: () => {},
};

export default TeachersInfo;
