import { Types } from '../types';

const initialState = {
  isLoading: false,
  hasError: false,
  deleteSuccess: false,
  errorMsg: '',
};

export function deleteAdminReducer(state = initialState, action) {
  switch (action.type) {
    case Types.DELETE_ADMIN_REQUEST:
      return {
        ...state,
        isLoading: true,
      };
    case Types.DELETE_ADMIN_SUCCESS:
      return {
        ...state,
        isLoading: false,
        hasError: false,
        deleteSuccess: true,
      };
    case Types.DELETE_ADMIN_FAILURE:
      return {
        ...state,
        isLoading: false,
        hasError: true,
        deleteSuccess: false,
        errorMsg: action.payload,
      };
    case Types.DELETE_ADMIN_RESET_STATE:
      return initialState;
    default:
      return state;
  }
}
