import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import './ForgetPassword.scss';

import AuthContainer from '../../components/AuthContainer';
import Alert from '../../components/Alert';
import FirstStep from './FirstStep';
import SecondStep from './SecondStep';

import icWarning from '../../assets/images/ic_warning.svg';

const ForgetPassword = ({ history }) => {
  const [step, setStep] = useState(1);
  const [username, setUsername] = useState('');
  const [alertMessage, setAlertMessage] = useState('');
  const [error, setError] = useState();

  const nextStep = () => {
    switch (step) {
      case 2:
        break;
      default:
        setStep(step + 1);
        break;
    }
  };

  const returnForLogin = () => history.push('/login');

  const handlerSubmit = () => {
    nextStep();
    if (error) {
      setAlertMessage(
        <Alert type="danger" messageAlert="Não implementado" icon={icWarning} altIcon="simbolo de atenção" />
      );
    }
  };

  return (
    <>
      <AuthContainer>
        {step === 1 ? (
          <FirstStep state={username} setState={setUsername} alertMessage={alertMessage} eventButton={handlerSubmit} />
        ) : (
          <SecondStep eventButton={returnForLogin} />
        )}
      </AuthContainer>
    </>
  );
};

ForgetPassword.propTypes = {
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
};

export default withRouter(ForgetPassword);
