import { Types } from '../types';

const initialState = {
  data: null,
  isLoading: false,
  hasError: false,
  errorMsg: '',
};

export function updateMentorReducer(state = initialState, action) {
  const { data } = action.payload || state;
  switch (action.type) {
    case Types.UPDATE_MENTOR_REQUEST:
      return {
        ...state,
        isLoading: true,
        hasError: false,
      };
    case Types.UPDATE_MENTOR_SUCCESS:
      return {
        ...state,
        isLoading: false,
        hasError: false,
        data,
      };
    case Types.UPDATE_MENTOR_FAILURE:
      return {
        ...state,
        isLoading: false,
        hasError: true,
        errorMsg: action.payload,
      };
    case Types.UPDATE_MENTOR_RESET_STATE:
      return initialState;
    default:
      return state;
  }
}
