export const enableButton = ({ to, from }) => {
  const hasValuesInFilter = !!(to && from);
  const enable = !!(hasValuesInFilter && from < to);
  return enable;
};

export const makeQueryString = data => {
  const convertObjectToQueryString = item => (data[item] ? `${item}=${data[item]}` : '');

  const keys = Object.keys(data);

  const mountQueryString = keys.map(convertObjectToQueryString);

  const removeNullItems = mountQueryString.filter(item => item);

  const joinedData = removeNullItems.join('&');

  return joinedData ? `?${joinedData}` : joinedData;
};
