import { Types } from '../types';

const initialState = {
  user: null,
  isLoading: false,
  hasError: false,
  errorMsg: '',
};

export function createAdminReducer(state = initialState, action) {
  const { user } = action.payload || state;
  switch (action.type) {
    case Types.CREATE_ADMIN_REQUEST:
      return {
        ...state,
        isLoading: true,
        hasError: false,
      };
    case Types.CREATE_ADMIN_SUCCESS:
      return {
        ...state,
        isLoading: false,
        hasError: false,
        user,
      };
    case Types.CREATE_ADMIN_FAILURE:
      return {
        ...state,
        isLoading: false,
        hasError: true,
        errorMsg: action.payload,
      };
    case Types.CREATE_ADMIN_RESET_STATE: // TODO - need refactory.
      return initialState;
    default:
      return state;
  }
}
