import { Api } from '../services/api';
import { commonHeaders } from '../utils/commonHeaders';

const getRoute = '/students';

function getStudents(offset = 0, authToken, search, mesalva) {
  const finalRoute = search ? `${getRoute}?${search}` : getRoute;
  const params = {
    limit: 10,
    offset,
    mesalva,
  };
  if (mesalva === 'null') {
    delete params.mesalva;
  }
  return Api.get(finalRoute, {
    params,
    headers: commonHeaders(authToken),
  });
}

function deleteStudent(id, authToken) {
  return Api.delete(`${getRoute}/${id}`, {
    headers: commonHeaders(authToken),
  });
}

export { getStudents, deleteStudent };
