import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter, useLocation } from 'react-router-dom';

import { Table } from '../../components/Table';
import Button from '../../components/Button';
import { ModalToRegisterMacrothemes } from './ModalToRegisterMacrothemes';
import { ModalDispatch } from '../../contexts/ModalContext';
import { LoadingDispatch } from '../../contexts/LodingContext';

import { AuthTokenSelector } from '../../Redux/Auth/selector';
import { createMacrothemesResetState } from '../../Redux/Macrothemes/Create/actions';
import { retrieveMacrothemessRowsTotalOffsetRequest } from '../../Redux/Macrothemes/Retrieve/actions';
import { updateMacrothemesReset } from '../../Redux/Macrothemes/Update/actions';
import { deleteMacrothemesReset } from '../../Redux/Macrothemes/Delete/actions';
import {
  MacrothemessRetrieveAllSelectors,
  MacrothemessCreateHasErrorSelectors,
  MacrothemesSuccessSelectors,
  MacrothemessCreateLoading,
  MacrothemessCreateErrorMessage,
  MacrothemesUpdateLoading,
  MacrothemesUpdateSelectors,
  MacrothemesDeleteLoading,
  MacrothemesDeleteSelectors,
} from '../../Redux/Macrothemes/selectors';

import { MacrothemessInfo } from './NewMacrothemessInfo';

import { showNotify } from '../../utils/showNotify';

function Macrothemess() {
  const dispatch = useDispatch();
  const authToken = useSelector(AuthTokenSelector);
  const createUserLoading = useSelector(MacrothemessCreateLoading);
  const deleteUserLoading = useSelector(MacrothemesDeleteLoading);
  const [valuePagination, setValuePagination] = useState(0);
  const modalDispatch = ModalDispatch();
  const loadingDispatch = LoadingDispatch();

  const Titles = ['Data', 'Modificado em', 'Nome', 'Faixa etária inicial', 'Faixa etária final'];

  const listOfKeys = [
    { key: 'created_at', isDate: true },
    { key: 'modified_at', isDate: true },
    { key: 'name' },
    { key: 'from_age_formatted' },
    { key: 'to_age_formatted' },
  ];

  const { search } = useLocation();
  const finalSearch = search && search.split('?')[1];
  const dispatchDataForTable = offset =>
    dispatch(retrieveMacrothemessRowsTotalOffsetRequest(offset, authToken, finalSearch));
  useEffect(() => {
    dispatchDataForTable(valuePagination);
  }, [valuePagination]);
  const dataOfTable = useSelector(MacrothemessRetrieveAllSelectors);
  const macrothemesUpdateLoading = useSelector(MacrothemesUpdateLoading);

  createUserLoading || macrothemesUpdateLoading || deleteUserLoading
    ? loadingDispatch({ type: 'open' })
    : loadingDispatch({ type: '' });

  const macrothemes = useSelector(MacrothemesSuccessSelectors);
  const createUserError = useSelector(MacrothemessCreateHasErrorSelectors);
  const updateMacrothemes = useSelector(MacrothemesUpdateSelectors);
  const deleteMacrothemes = useSelector(MacrothemesDeleteSelectors);
  const errorMsg = useSelector(MacrothemessCreateErrorMessage);
  function openModalToRegisterMacrothemes() {
    modalDispatch({ type: 'open', payload: <ModalToRegisterMacrothemes /> });
  }

  function getDataColapse(userID) {
    return <MacrothemessInfo useForEdit idMacrothemes={userID} />;
  }

  function createMacrothemesNotify() {
    let contentNotify;
    let typeNotify;
    if (createUserError) {
      typeNotify = 'error';
      contentNotify = `Ocorreu um erro ao cadastrar o macrothemes. ${errorMsg}. Tente novamente :)`;
    } else if (macrothemes) {
      typeNotify = 'success';
      contentNotify = 'Macrotema cadastrado com sucesso!';
      dispatch(createMacrothemesResetState()); // TODO - need refactory.
      dispatchDataForTable(valuePagination);
    }
    showNotify(typeNotify, contentNotify);
  }

  function updateMacrothemesNotify() {
    let contentNotify;
    let typeNotify;
    if (updateMacrothemes.hasError) {
      typeNotify = 'error';
      contentNotify = 'Ocorreu um erro ao atualizar cadastro do macrothemes. Tente novamente :)';
    } else if (updateMacrothemes.data) {
      typeNotify = 'success';
      contentNotify = `Macrotema ${updateMacrothemes.data.name} atualizado com sucesso!`;
      dispatch(updateMacrothemesReset()); // TODO - need refactory.
      dispatchDataForTable(valuePagination); // TODO - not working
    }
    showNotify(typeNotify, contentNotify);
  }

  function deleteMacrothemesNotify() {
    let contentNotify;
    let typeNotify;
    if (deleteMacrothemes.hasError) {
      typeNotify = 'error';
      contentNotify = 'Ocorreu um erro ao excluir cadastro do Artigo. Tente novamente :)';
    } else if (deleteMacrothemes.deleteSuccess) {
      typeNotify = 'success';
      contentNotify = 'Artigo excluido com sucesso!';
      dispatch(deleteMacrothemesReset()); // TODO - need refactory.
      dispatchDataForTable(valuePagination); // TODO - not working
    }
    showNotify(typeNotify, contentNotify);
  }

  return (
    <>
      {(macrothemes || createUserError) && createMacrothemesNotify()}
      {(updateMacrothemes.data || updateMacrothemes.hasError) && updateMacrothemesNotify()}
      {(deleteMacrothemes.deleteSuccess || deleteMacrothemes.hasError) && deleteMacrothemesNotify()}

      <div className="row mb-2">
        <div className="col-10">
          <h3 className="titlePages">Macrotemas</h3>
        </div>

        <div className="col-2">
          <Button inputFilled handleClick={openModalToRegisterMacrothemes}>
            Cadastrar
          </Button>
        </div>
      </div>

      <Table
        titles={Titles}
        dataOfTable={dataOfTable}
        keysOfObjectsData={listOfKeys}
        dataKey="id"
        itemColapse={getDataColapse}
        functionForPagination={setValuePagination}
      />
    </>
  );
}

export default withRouter(Macrothemess);
