import { Types } from '../types';

export function deleteAdminRequest({ id, authToken }) {
  return {
    type: Types.DELETE_ADMIN_REQUEST,
    payload: { id, authToken },
  };
}

export function deleteAdminSuccess() {
  return {
    type: Types.DELETE_ADMIN_SUCCESS,
  };
}

export function deleteAdminFailure(errorMessage) {
  return {
    type: Types.DELETE_ADMIN_FAILURE,
    payload: errorMessage,
  };
}

export function deleteAdminReset() {
  return {
    type: Types.DELETE_ADMIN_RESET_STATE,
  };
}
