import React from 'react';
import PropTypes from 'prop-types';
import { BUTTON_CLASSES } from '../../utils/enums';
import './Button.scss';

function Button({ children, handleClick, type, classes,dataTest }) {
  return (
    // eslint-disable-next-line react/button-has-type
    <button data-test={dataTest} onClick={handleClick} type={type} className={`button button${classes}`}>
      {children}
    </button>
  );
}
Button.propTypes = {
  children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node]).isRequired,
  handleClick: PropTypes.func,
  type: PropTypes.string,
  classes: PropTypes.oneOf(['--Await ', '--noAwait', '--grey']),
  dataTest: PropTypes.string,
};

Button.defaultProps = {
  type: 'button',
  handleClick: () => {},
  classes: BUTTON_CLASSES.NO_AWAIT,
};

export default Button;
