import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter, useLocation } from 'react-router-dom';

import { Table } from '../../components/Table';
import Button from '../../components/Button';
import { ModalToRegisterAdmin } from './ModalToRegisterAdmin';
import { ModalDispatch } from '../../contexts/ModalContext';
import { LoadingDispatch } from '../../contexts/LodingContext';

import { AuthTokenSelector } from '../../Redux/Auth/selector';
import { createAdminResetState } from '../../Redux/Admin/Create/actions';
import { retrieveAdminsRowsTotalOffsetRequest } from '../../Redux/Admin/Retrieve/actions';
import { updateAdminReset } from '../../Redux/Admin/Update/actions';
import { deleteAdminReset } from '../../Redux/Admin/Delete/actions';
import {
  AdminsRetrieveAllSelectors,
  AdminsCreateHasErrorSelectors,
  AdminsCreateGetUserSelectors,
  AdminsCreateLoading,
  AdminsCreateErrorMessage,
  AdminUpdateLoading,
  AdminUpdateSelectors,
  AdminDeleteLoading,
  AdminDeleteSelectors,
} from '../../Redux/Admin/selectors';

import { AdminsInfo } from './NewAdminsInfo';

import { showNotify } from '../../utils/showNotify';

function Admins() {
  const dispatch = useDispatch();
  const authToken = useSelector(AuthTokenSelector);
  const createUserLoading = useSelector(AdminsCreateLoading);
  const deleteUserLoading = useSelector(AdminDeleteLoading);
  const [valuePagination, setValuePagination] = useState(0);
  const modalDispatch = ModalDispatch();
  const loadingDispatch = LoadingDispatch();

  const Titles = ['Data', 'Modificado em', 'Nome', 'Email'];

  const listOfKeys = [
    { key: 'created_at', isDate: true },
    { key: 'modified_at', isDate: true },
    { key: 'name' },
    { key: 'username' },
  ];

  const { search } = useLocation();
  const finalSearch = search && search.split('?')[1];
  const dispatchDataForTable = offset => dispatch(retrieveAdminsRowsTotalOffsetRequest(offset, authToken, finalSearch));
  useEffect(() => {
    dispatchDataForTable(valuePagination);
  }, [valuePagination]);
  const dataOfTable = useSelector(AdminsRetrieveAllSelectors);
  const adminUpdateLoading = useSelector(AdminUpdateLoading);

  createUserLoading || adminUpdateLoading || deleteUserLoading
    ? loadingDispatch({ type: 'open' })
    : loadingDispatch({ type: '' });

  const user = useSelector(AdminsCreateGetUserSelectors);
  const createUserError = useSelector(AdminsCreateHasErrorSelectors);
  const updateAdmin = useSelector(AdminUpdateSelectors);
  const deleteAdmin = useSelector(AdminDeleteSelectors);
  const errorMsg = useSelector(AdminsCreateErrorMessage);

  function openModalToRegisterAdmin() {
    modalDispatch({ type: 'open', payload: <ModalToRegisterAdmin /> });
  }

  function getDataColapse(userID) {
    return <AdminsInfo useForEdit idAdmin={userID} />;
  }

  function createAdminNotify() {
    let contentNotify;
    let typeNotify;
    if (createUserError) {
      typeNotify = 'error';
      contentNotify = `Ocorreu um erro ao cadastrar o admin. ${errorMsg}. Tente novamente :)`;
    } else if (user) {
      typeNotify = 'success';
      contentNotify = `Administrador(a) ${user.user.name} cadastrado(a) com sucesso! senha padrão 12345678`;
      dispatch(createAdminResetState()); // TODO - need refactory.
      dispatchDataForTable(valuePagination);
    }
    showNotify(typeNotify, contentNotify);
  }

  function updateAdminNotify() {
    let contentNotify;
    let typeNotify;
    if (updateAdmin.hasError) {
      typeNotify = 'error';
      contentNotify = 'Ocorreu um erro ao atualizar cadastro do admin. Tente novamente :)';
    } else if (updateAdmin.data) {
      typeNotify = 'success';
      contentNotify = `Admin(a) ${updateAdmin.data.name} atualizado(a) com sucesso!`;
      dispatch(updateAdminReset()); // TODO - need refactory.
      dispatchDataForTable(valuePagination); // TODO - not working
    }
    showNotify(typeNotify, contentNotify);
  }

  function deleteAdminNotify() {
    let contentNotify;
    let typeNotify;
    if (deleteAdmin.hasError) {
      typeNotify = 'error';
      contentNotify = 'Ocorreu um erro ao excluir cadastro do admin. Tente novamente :)';
    } else if (deleteAdmin.deleteSuccess) {
      typeNotify = 'success';
      contentNotify = 'Admin excluido com sucesso!';
      dispatch(deleteAdminReset()); // TODO - need refactory.
      dispatchDataForTable(valuePagination); // TODO - not working
    }
    showNotify(typeNotify, contentNotify);
  }

  return (
    <>
      {(user || createUserError) && createAdminNotify()}
      {(updateAdmin.data || updateAdmin.hasError) && updateAdminNotify()}
      {(deleteAdmin.deleteSuccess || deleteAdmin.hasError) && deleteAdminNotify()}

      <div className="row mb-2">
        <div className="col-10">
          <h3 className="titlePages">Admins</h3>
        </div>

        <div className="col-2">
          <Button inputFilled handleClick={openModalToRegisterAdmin}>
            Cadastrar
          </Button>
        </div>
      </div>

      <Table
        titles={Titles}
        dataOfTable={dataOfTable}
        keysOfObjectsData={listOfKeys}
        dataKey="user_id"
        itemColapse={getDataColapse}
        functionForPagination={setValuePagination}
      />
    </>
  );
}

export default withRouter(Admins);
