import { call, put } from 'redux-saga/effects';

import jwt from 'jsonwebtoken';
import { Api } from '../../services/api';
import { history } from '../../services/history';
import { loginRequest } from '../../client/auth';
import { signInSuccess, signInFailure, signInForbidden } from '../../Redux/Auth/actions';

export function* signInSaga({ payload }) {
  try {
    const { username, password } = payload;

    const response = yield call(loginRequest, { username, password });
    const { data: authToken } = response;
    const user = jwt.decode(authToken);
    const { role: alias } = user;

    if (alias !== 'admin') {
      const message = setSuccessMessage(alias);
      yield put(signInForbidden(message));
    } else {
      Api.defaults.headers.Authorization = `Bearer ${authToken}`;

      yield put(signInSuccess(authToken, user));

      history.push('/');
    }
  } catch (err) {
    const errorMsg = setErrorMessage(err.response);
    yield put(signInFailure(errorMsg));
  }
}

function setSuccessMessage(alias) {
  let role = 'professor';
  if (alias === 'student') {
    role = 'aluno';
  } else if (alias !== 'Professor' && alias !== 'admin') {
    return 'Ops!, algo inesperado ocorreu. Tente novamente mais tarde';
  }
  return `Ops! Seu login é de ${role}, por favor  utilize o aplicativo disponível para Android e iOS.`;
}

function setErrorMessage(errResponse) {
  let errorMsg = '';
  if (errResponse) {
    const { status } = errResponse;
    if (status === 404) {
      errorMsg = 'E-mail ou senha inválidos.';
    } else {
      errorMsg = 'Desculpe, e-mail não autorizado.';
    }
  } else {
    errorMsg = 'Falha ao conectar. Por favor, verifique sua conexão com a Internet e tente novamente.';
  }
  return errorMsg;
}
