import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import Button from '../../../Button';

import icLogout from '../../../../assets/images/ic_logout.svg';

function ViewDataUser({ dataOfUserToList, logoutOnClick, changeToEdit }) {
  return (
    <>
      <div className="d-flex flex-column mt-4">
        {dataOfUserToList.map(val => {
          const { label, value } = val;
          return (
            <div
              key={`${value}-${label}`}
              className="d-flex justify-content-between bodyEditSelfUser__dividerLines bodyEditSelfUser__fontSize"
            >
              <div className="d-flex justify-content-start bodyEditSelfUser__greyText">{label}</div>
              <div className="d-flex justify-content-end">{value}</div>
            </div>
          );
        })}
      </div>

      <div className="ml-1 mr-1 mt-5 mb-4">
        <Button inputFilled handleClick={changeToEdit}>
          Editar
        </Button>
      </div>

      <div className="mt-4 d-flex justify-content-end bodyEditSelfUser__greyText">
        <Link to="/login" onClick={logoutOnClick}>
          <img className="iconLogout" src={icLogout} alt="closed" />
          Sair da Conta
        </Link>
      </div>
    </>
  );
}

ViewDataUser.propTypes = {
  dataOfUserToList: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string.isRequired,
      value: PropTypes.any.isRequired,
    })
  ).isRequired,
  logoutOnClick: PropTypes.func.isRequired,
  changeToEdit: PropTypes.func.isRequired,
};

export default ViewDataUser;
