import { Types } from '../types';

const initialState = {
  limit: 0,
  offset: 0,
  total: 0,
  rows: [],
  isLoading: false,
  hasError: false,
  errorMsg: '',
};

function retrieveAdminsRowsTotalOffsetReducer(state = initialState, action) {
  const { rows, total, offset } = action.payload || initialState;
  switch (action.type) {
    case Types.RETRIEVE_ALL_ADMINS_REQUEST:
      return {
        ...state,
        isLoading: true,
        hasError: false,
        errorMsg: '',
      };
    case Types.RETRIEVE_ALL_ADMINS_SUCCESS:
      return {
        ...state,
        rows,
        total,
        offset,
        isLoading: false,
        hasError: false,
        errorMsg: '',
      };
    case Types.RETRIEVE_ALL_ADMINS_FAILURE:
      return {
        ...state,
        isLoading: false,
        hasError: true,
        errorMsg: action.payload,
      };
    default:
      return state;
  }
}

export { retrieveAdminsRowsTotalOffsetReducer };
