import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import './TopBar.scss';

import WidgetUser from './WidgetUser';
import { InputTopBar } from './InputTopBar';
import Logo from '../Logo';

import { AdminUpdateSelf } from '../../Redux/Admin/selectors';
import { updateSelfReset } from '../../Redux/Admin/UpdatedSelf/actions';

import { LoadingDispatch } from '../../contexts/LodingContext';

import { showNotify } from '../../utils/showNotify';

function TopBar() {
  const dispatch = useDispatch();
  const loadingDispatch = LoadingDispatch();
  const { hasError, data } = useSelector(AdminUpdateSelf);

  function reponseOfUpdate() {
    let typeNotify;
    let message;
    if (hasError) {
      typeNotify = 'error';
      message = 'Ocorreu um erro ao atualizar os dados. Tente novamente :)';
    } else {
      typeNotify = 'success';
      message = 'Dados alterados com sucesso!';
    }

    loadingDispatch({ type: '' });
    showNotify(typeNotify, message);
    dispatch(updateSelfReset());
  }

  (data || hasError) && reponseOfUpdate();
  return (
    <div className="topBar d-flex">
      <div className="topBar__logo">
        <Logo alt="texto gerando falcões e falcão acima do texto" title="Logo Gerando Falcões" height={40} />
      </div>

      <InputTopBar />

      <div className="topBar__rightSide">
        <div className="topBar__rightSide__contentUser">
          <WidgetUser />
        </div>
      </div>
    </div>
  );
}

export default TopBar;
