import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';

import { Select } from '../../../components/Select';
import Thumbnail from '../../../components/Thumbnail';
import Input from '../../../components/Input';
import Button from '../../../components/Button';

import listOfState from '../../../utils/listOfState';
import { maskToTelephone } from '../../../utils/maskToInputs';

import { MentorsRetrieveAllSelectors } from '../../../Redux/Mentor/selectors';
import { AuthTokenSelector } from '../../../Redux/Auth/selector';

import { createMentorRequest } from '../../../Redux/Mentor/Create/actions';
import { updateMentorRequest } from '../../../Redux/Mentor/Update/actions';
import { deleteMentorRequest } from '../../../Redux/Mentor/Delete/actions';

import { ModalDispatch } from '../../../contexts/ModalContext';
import { ModalToDeleteMentor } from '../ModalToDeleteMentor';

function MentorsInfo({ idMentor, useForEdit, closeModalCreate }) {
  const dispatch = useDispatch();
  const [name, setName] = useState();
  const [cellphone, setCellphone] = useState();
  const [email, setEmail] = useState();
  const [countryState, setCountryState] = useState();
  const [city, setCity] = useState();

  const textButton = useForEdit ? 'Atualizar' : 'Cadastrar';

  const authToken = useSelector(AuthTokenSelector);
  const modalDispatch = ModalDispatch();

  function createMentor(e) {
    e.preventDefault();
    const parameters = {
      data: { name, email },
      authToken,
    };
    dispatch(createMentorRequest(parameters));
    closeModalCreate();
  }

  function updateMentor(e) {
    e.preventDefault();
    const parameters = {
      id: idMentor,
      data: { name },
      authToken,
    };
    dispatch(updateMentorRequest(parameters));
  }

  function modalSelect(e) {
    e.preventDefault();
    const payload = <ModalToDeleteMentor nameMentor={name} actionIfYes={deleteMentor} />;
    modalDispatch({ type: 'open', payload });
  }
  function deleteMentor() {
    dispatch(deleteMentorRequest({ id: idMentor, authToken }));
    modalDispatch({ type: '' });
  }

  const finalDispatch = useForEdit ? updateMentor : createMentor;

  function startComponent() {
    if (useForEdit) {
      const { name: Name, username } = mentors.rows.find(val => val.user_id === idMentor);
      setName(Name);
      setEmail(username);
    }
  }

  useEffect(() => {
    startComponent();
  }, []);
  const mentors = useSelector(MentorsRetrieveAllSelectors);

  return (
    <>
      <div>
        <Thumbnail alt="foto do professor" height="104px" />
      </div>

      <div className="mt-4 row">
        <form onSubmit={finalDispatch} className="col-12">
          <div className="row">
            <div className="col-4 mr-4">
              <label htmlFor="name">Nome</label>
              <Input id="name" inputValue={name} setInputValue={setName} required maxLength={40} />
            </div>

            <div className="col-4 d-none">
              <label htmlFor="cellphone">DDD + Telefone</label>
              <Input
                id="cellphone"
                inputValue={maskToTelephone(cellphone)}
                typeInput="number"
                setInputValue={setCellphone}
                maxLength={15}
              />
            </div>
          </div>

          <div className="row mt-4">
            <div className="col-4 mr-4">
              <label htmlFor="email">Email</label>
              <Input
                id="email"
                readOnly={useForEdit}
                inputValue={email}
                typeInput="email"
                setInputValue={setEmail}
                required
              />
            </div>

            <div className="col-4 d-none">
              <div className="row">
                <div className="col-6">
                  <label htmlFor="state">Estado</label>
                  <br />
                  <Select valueOfSelect={countryState} setValueOfSelect={setCountryState}>
                    {listOfState.map(val => (
                      <option key={val.initials} value={val.initials}>
                        {val.name}
                      </option>
                    ))}
                  </Select>
                </div>

                <div className="col-6">
                  <label htmlFor="city">Cidade</label>
                  <br />
                  <Select valueOfSelect={city} setValueOfSelect={setCity}>
                    {listOfState.map(val => (
                      <option key={val.initials} value={val.initials}>
                        {val.name}
                      </option>
                    ))}
                  </Select>
                </div>
              </div>
            </div>
          </div>

          <div className="d-flex justify-content-end mt-1 mb-1">
            {useForEdit && (
              <div className="col-2">
                <Button inputFilled handleClick={modalSelect} type="button" grey>
                  Excluir
                </Button>
              </div>
            )}
            <div className="col-2">
              <Button inputFilled type="submit">
                {textButton}
              </Button>
            </div>
          </div>
        </form>
      </div>
    </>
  );
}

MentorsInfo.propTypes = {
  idMentor: PropTypes.number,
  useForEdit: PropTypes.bool,
  closeModalCreate: PropTypes.func,
};

MentorsInfo.defaultProps = {
  idMentor: 0,
  useForEdit: false,
  closeModalCreate: () => {},
};

export default MentorsInfo;
