import { DashboardTypes } from '../types';

const enumTitle = {
  new_classes: { title: 'Aulas criadas', order: 3 },
  new_students: { title: 'Novos alunos', order: 5 },
  new_teachers: { title: 'Novos professores', order: 4 },
  students_voucher: { title: 'Alunos no MeSalva', order: 6 },
  class_contents_accesses: { title: 'Acesso aos itens das aula', order: 7 },
  students_accesses: { title: 'Acessos alunos', order: 2 },
  teacher_accesses: { title: 'Acessos professores', order: 1 },
};

function FormatValue(backendTitle, value) {
  if ((backendTitle === 'new_students' || backendTitle === 'new_teachers') && value > 0) {
    return `+${value}`;
  }
  return value;
}

function formatData(data) {
  return data
    .map(item => {
      const { title: backendTitle, value } = item;
      const { title, order } = enumTitle[backendTitle];
      return { backendTitle, value: FormatValue(backendTitle, value), title, order };
    })
    .sort((a, b) => a.order - b.order);
}

const initialState = {
  dashboard: [],
  isLoading: false,
  hasError: false,
  errorMsg: '',
};

export function retrieveAllDashboardReducer(state = initialState, action) {
  switch (action.type) {
    case DashboardTypes.RETRIEVE_ALL_DASHBOARD_REQUEST:
      return {
        ...state,
        isLoading: true,
      };
    case DashboardTypes.RETRIEVE_ALL_DASHBOARD_SUCCESS:
      return {
        ...state,
        isLoading: false,
        dashboard: formatData(action.payload),
      };
    case DashboardTypes.RETRIEVE_ALL_DASHBOARD_FAILURE:
      return {
        ...state,
        isLoading: false,
        hasError: true,
        errorMsg: action.payload,
      };
    default:
      return state;
  }
}
