import React from 'react';
import { Switch } from 'react-router-dom';

import { RouteWrapper as Route } from './routeWrapper';
import TemplateInternal from '../components/TemplateInternal';
import { Login } from '../pages/Login';
import ForgetPassword from '../pages/ForgetPassword';
import Mentors from '../pages/Mentors';
import Macrothemess from '../pages/Macrothemess';
import Unitss from '../pages/Unitss';
import Workshops from '../pages/Workshops';
import Admins from '../pages/Admins';
import Teachers from '../pages/Teachers';
import { Dashboard } from '../pages/Dashboard';
import { Classes } from '../pages/Classes';
import { Students } from '../pages/Students';

import { ModalProvider } from '../contexts/ModalContext';
import { NotifyProvider } from '../contexts/NotifyContext';
import { LoadingProvider } from '../contexts/LodingContext';

function Routes() {
  return (
    <Switch>
      <Route path="/login" exact component={Login} />
      <Route path="/forgetpassword" exact component={ForgetPassword} />

      <ModalProvider>
        <NotifyProvider>
          <LoadingProvider>
            <TemplateInternal>
              <Route path="/dashboard" exact component={Dashboard} isPrivate />
              <Route path="/professores" exact component={Teachers} isPrivate />
              <Route path="/aulas" exact component={Classes} isPrivate />
              <Route path="/alunos" exact component={Students} isPrivate />
              <Route path="/mentores" exact component={Mentors} isPrivate />
              <Route path="/artigos" exact component={Macrothemess} isPrivate />
              <Route path="/unidades" exact component={Unitss} isPrivate />
              <Route path="/oficinas" exact component={Workshops} isPrivate />
              <Route path="/admins" exact component={Admins} isPrivate />
            </TemplateInternal>
          </LoadingProvider>
        </NotifyProvider>
      </ModalProvider>
    </Switch>
  );
}
export { Routes };
