import { Types } from '../types';

const initialState = {
  limit: 0,
  offset: 0,
  total: 0,
  rows: [],
  isLoading: false,
  hasError: false,
  errorMsg: '',
};

export function retrieveClassesRowsTotalOffsetReducer(state = initialState, action) {
  const { rows, total, offset } = action.payload || state;
  switch (action.type) {
    case Types.RETRIEVE_ALL_CLASSES_REQUEST:
      return {
        ...state,
        isLoading: true,
      };
    case Types.RETRIEVE_ALL_CLASSES_SUCCESS:
      return {
        ...state,
        rows,
        total,
        offset,
        isLoading: false,
      };
    case Types.RETRIEVE_ALL_CLASSES_FAILURE:
      return {
        ...state,
        isLoading: false,
        hasError: true,
        errorMsg: action.payload,
      };
    default:
      return state;
  }
}
