import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import './style.scss';

import Input from '../../../components/Input';
import Button from '../../../components/Button';
import { Select } from '../../../components/Select';
import listOfState from '../../../utils/listOfState';
import { RetrieveCitiesSelectors } from '../../../Redux/Cities/selectors';
import { retriveCitiesOfStateReducerRequest } from '../../../Redux/Cities/Retrieve/actions';

import { UnitssRetrieveAllSelectors } from '../../../Redux/Units/selectors';
import { AuthTokenSelector } from '../../../Redux/Auth/selector';

import { createUnitsRequest } from '../../../Redux/Units/Create/actions';
import { updateUnitsRequest } from '../../../Redux/Units/Update/actions';
import { deleteUnitsRequest } from '../../../Redux/Units/Delete/actions';

import { ModalDispatch } from '../../../contexts/ModalContext';
import { ModalToDeleteUnits } from '../ModalToDeleteUnits';

function UnitssInfo({ idUnits, useForEdit, closeModalCreate }) {
  const dispatch = useDispatch();
  const [name, setName] = useState();
  const [state, setState] = useState('AC');
  const [city, setCity] = useState('Acrelândia');
  const [siteCode, setSiteCode] = useState('');

  const textButton = useForEdit ? 'Atualizar' : 'Cadastrar';

  const authToken = useSelector(AuthTokenSelector);
  const modalDispatch = ModalDispatch();

  function createUnits(e) {
    e.preventDefault();
    const parameters = {
      data: { name, city, state, site_code: siteCode },
      authToken,
    };
    dispatch(createUnitsRequest(parameters));
    closeModalCreate();
  }

  function updateUnits(e) {
    e.preventDefault();
    const parameters = {
      id: idUnits,
      data: { name, city, state, site_code: siteCode },
      authToken,
    };
    dispatch(updateUnitsRequest(parameters));
  }

  function modalSelect(e) {
    e.preventDefault();
    const payload = <ModalToDeleteUnits nameUnits={name} actionIfYes={deleteUnits} />;
    modalDispatch({ type: 'open', payload });
  }

  function deleteUnits() {
    dispatch(deleteUnitsRequest({ id: idUnits, authToken }));
    modalDispatch({ type: '' });
  }

  const finalDispatch = useForEdit ? updateUnits : createUnits;

  function startComponent() {
    if (useForEdit) {
      const { name: Name, state: State, city: City, site_code: siteCode } = unitss.rows.find(val => val.id === idUnits);
      setName(Name);
      setName(Name);
      setState(State);
      setCity(City);
      setSiteCode(siteCode);
    }
  }

  function getCities() {
    dispatch(retriveCitiesOfStateReducerRequest(state, authToken));
  }

  useEffect(() => {
    getCities();
  }, [state]);

  useEffect(() => {
    startComponent();
  }, []);

  const unitss = useSelector(UnitssRetrieveAllSelectors);
  const cities = useSelector(RetrieveCitiesSelectors).sort();

  return (
    <>
      <div className="mt-6 row">
        <form onSubmit={finalDispatch} className="col-12">
          <div className="row">
            <div className="col-4 mr-4">
              <label htmlFor="name">Nome</label>
              <Input id="name" inputValue={name} setInputValue={setName} required maxLength={40} />
            </div>
            <div className="col-4 mr-4">
              <label htmlFor="name">Codigo do Site</label>
              <Input name="site_code" inputValue={siteCode} setInputValue={setSiteCode} required maxLength={40} />
            </div>
          </div>
          <div className="col-4 mr-4" id="box-select">
            <div className="row">
              <div className="col-6">
                <label htmlFor="state">Estado</label>
                <br />
                <Select valueOfSelect={state} setValueOfSelect={setState}>
                  {listOfState.map(val => (
                    <option key={val.initials} value={val.initials}>
                      {val.name}
                    </option>
                  ))}
                </Select>
              </div>

              <div className="col-6">
                <label htmlFor="city">Cidade</label>
                <br />
                <Select valueOfSelect={city} setValueOfSelect={setCity} deactivate={!cities.length > 0}>
                  {cities.map(val => (
                    <option key={val} value={val}>
                      {val}
                    </option>
                  ))}
                </Select>
              </div>
            </div>
          </div>

          <div className="d-flex justify-content-end mt-1 mb-1">
            {useForEdit && (
              <div className="col-2">
                <Button inputFilled handleClick={modalSelect} type="button" grey>
                  Excluir
                </Button>
              </div>
            )}
            <div className="col-2">
              <Button inputFilled type="submit">
                {textButton}
              </Button>
            </div>
          </div>
        </form>
      </div>
    </>
  );
}

UnitssInfo.propTypes = {
  idUnits: PropTypes.number,
  useForEdit: PropTypes.bool,
  closeModalCreate: PropTypes.func,
};

UnitssInfo.defaultProps = {
  idUnits: 0,
  useForEdit: false,
  closeModalCreate: () => {},
};

export default UnitssInfo;
