import { Api } from '../services/api';
import { commonHeaders } from '../utils/commonHeaders';

const getRoute = '/classes';
const getClassContent = `${getRoute}/contents`;
const deleteRoute = '/classes';

function getClasses(offset = 0, authToken, search) {
  const finalRoute = search ? `${getClassContent}?${search}` : getClassContent;
  return Api.get(finalRoute, {
    params: {
      limit: 10,
      offset,
    },
    headers: commonHeaders(authToken),
  });
}

function getClassContentById(classid, authToken) {
  return Api.get(getClassContent, {
    params: { classid },
    headers: commonHeaders(authToken),
  });
}

function deleteClass(id, authToken) {
  return Api.delete(`${deleteRoute}/${id}`, {
    headers: commonHeaders(authToken),
  });
}

export { getClasses, getClassContentById, deleteClass };
