import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';

import { ModalToDeleteClass } from './ModalToDeleteClass';

import { AuthTokenSelector } from '../../Redux/Auth/selector';
import { StudentsRetrieveAllSelectors, ClassDeleteSelectors } from '../../Redux/Students/selectors';
import { retrieveStudentsRowsTotalOffsetRequest } from '../../Redux/Students/Retrieve/actions';

import { deleteStudentRequest, deleteStudentResetState } from '../../Redux/Students/Delete/actions';

import { Table } from '../../components/Table';
import { Select } from '../../components/Select';

import { LoadingDispatch } from '../../contexts/LodingContext';
import { ModalDispatch } from '../../contexts/ModalContext';

import { showNotify } from '../../utils/showNotify';

function Students() {
  const dispatch = useDispatch();
  const loadingDispatch = LoadingDispatch();
  const modalDispatch = ModalDispatch();
  const [valuePagination, setValuePagination] = useState(0);

  const [withVoucher, setWithVoucher] = useState(null);

  const Titles = ['Data', 'Modificado em', 'Nome', 'Data de nascimento', 'Estado', 'Cidade', 'Escola'];
  const listOfKeys = [
    { key: 'created_at', isDate: true },
    { key: 'modified_at', isDate: true },
    { key: 'name' },
    { key: 'birth_date', isDate: true },
    { key: 'state' },
    { key: 'city' },
    { key: 'school_name' },
  ];

  const authToken = useSelector(AuthTokenSelector);

  const getItemById = id => dataOfTable.rows.find(item => item.user_id === id);

  function deleteStudentNotify() {
    let contentNotify;
    let typeNotify;
    if (deleteStudent.hasError) {
      typeNotify = 'error';
      contentNotify = 'Ocorreu um erro ao excluir Aluno(a). Tente novamente :)';
    } else if (deleteStudent.deleteSuccess) {
      typeNotify = 'success';
      contentNotify = 'Aluno(a) excluido(a) com sucesso!';
      dispatch(deleteStudentResetState());
      dispatchDataForTable(valuePagination);
    }
    showNotify(typeNotify, contentNotify);
  }

  function deleteStudentByid(id) {
    dispatch(deleteStudentRequest(id, authToken));
    modalDispatch({ type: '' });
  }

  function openModalToDelete(id) {
    const { name } = getItemById(id);
    modalDispatch({
      type: 'open',
      payload: (
        <ModalToDeleteClass
          classTitle={name}
          actionIfYes={() => {
            deleteStudentByid(id);
          }}
        />
      ),
    });
  }

  function alternativeRowClick(id) {
    const data = getItemById(id);
  }

  const { search } = useLocation();
  const finalSearch = search && search.split('?')[1];
  function dispatchDataForTable(offset) {
    dispatch(retrieveStudentsRowsTotalOffsetRequest(offset, authToken, finalSearch, withVoucher));
  }

  useEffect(() => {
    dispatchDataForTable(valuePagination);
  }, [valuePagination, withVoucher]);

  const dataOfTable = useSelector(StudentsRetrieveAllSelectors);
  const deleteStudent = useSelector(ClassDeleteSelectors);

  deleteStudent.isLoading ? loadingDispatch({ type: 'open' }) : loadingDispatch({ type: '' });
  (deleteStudent.deleteSuccess || deleteStudent.hasError) && deleteStudentNotify();
  return (
    <>
      <div className="row mb-2">
        <div className="col-10">
          <h3 className="titlePages">Alunos</h3>
        </div>

        <div className="col-2">
          <Select valueOfSelect={withVoucher} setValueOfSelect={setWithVoucher}>
            <option value='null'>Todos alunos</option>
            <option value={true}>Alunos com voucher</option>
            <option value={false}>Aluno sem voucher</option>
          </Select>
        </div>
      </div>

      <Table
        titles={Titles}
        dataOfTable={dataOfTable}
        keysOfObjectsData={listOfKeys}
        dataKey="user_id"
        functionForPagination={setValuePagination}
        notColapse
        notColapseFunction={openModalToDelete}
        alternativeRowClick={alternativeRowClick}
      />
    </>
  );
}

export default Students;
